	module.exports = {"data":{"markdownRemark":{"html":"<h2>Contextual Inquiries</h2>\n<p>Contextual inquiry is a semi-structured interview method to obtain information about the context of use, where users are first asked a set of standard questions and then observed and questioned while they work in their own environments.</p>\n<p>Because users are interviewed in their own environments, the analysis data is more realistic than laboratory data. Contextual inquiry is based on a set of principles that allow it to be molded to different situations. This technique is generally used at the beginning of the design process and is good for getting rich information about work practices, the social, technical, and physical environments, and user tools.</p>\n<p>The four principles of contextual inquiry are:</p>\n<ul>\n<li>Focus - Plan for the inquiry, based on a clear understanding of your purpose</li>\n<li>Context - Go to the customer’s workplace and watch them do their own work</li>\n<li>Partnership - Talk to customers about their work and engage them in uncovering unarticulated aspects of work</li>\n<li>Interpretation - Develop a shared understanding with the customer about the aspects of work that matter</li>\n</ul>\n<p>The results of contextual inquiry can be used to define requirements, improve a process, learn what is important to users and customers, and just learn more about a new domain to inform future projects.</p>","fields":{"slug":"/user-experience-research/contextual-inquiries/"},"frontmatter":{"title":"Contextual Inquiries"}}},"pathContext":{"slug":"/user-experience-research/contextual-inquiries/"}}

